.tone-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
.tone-button {
    width: 100px;
    height: 100px;
    margin: 10px;
    border-radius: 10%;
    background-color: #eee;
    font-size: 20px;
}
.tone-control {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
